/* Global Header Styles */
header {
  width: 100%;
  background-color: #3498db; /* Beautiful blue header background */
  color: white;
  padding: 20px 0;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

/* Header Text */
header h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin: 0;
  font-family: 'Roboto', sans-serif; /* Modern font */
}

/* Adjust header spacing for small screens */
@media (max-width: 768px) {
  header h1 {
    font-size: 2rem;
  }
}
