body {
  font-family: "Inter", sans-serif;
  background-color: #fff;
  color: #333;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.recipe-search {
  padding: 50px 20px;
  max-width: 1200px;
  margin: 0 auto;
  transition: background-color 0.3s ease;
  border-radius: 15px;
}

/* Main header styling */
.main-header {
  background-color: #34495e;
  padding: 20px;
  text-align: center;
  color: white;
  margin-bottom: 30px; /* Space between header and content */
  position: relative; /* Reset positioning */
}

.main-header h1 {
  font-size: 2.5rem;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

/* Header actions (Profile, Logout, Dark Mode Toggle) */
.header-actions {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 10px;
}

.header-actions button {
  padding: 8px 15px;
  background-color: #2980b9;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.header-actions button:hover {
  background-color: #1f6b99;
}

.profile-btn {
  background-color: #3498db;
}

.profile-btn:hover {
  background-color: #2980b9;
}

.logout-btn {
  background-color: #e74c3c;
}

.logout-btn:hover {
  background-color: #c0392b;
}

.dark-mode-toggle {
  background-color: #2ecc71;
}

.dark-mode-toggle:hover {
  background-color: #27ae60;
}

/* Search bar styles */
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.search-bar input {
  width: 100%;
  max-width: 500px;
  padding: 12px 20px;
  border: 2px solid #ddd;
  border-radius: 25px;
  font-size: 1rem;
  outline: none;
  transition: border 0.3s ease;
}

.search-bar input:focus {
  border-color: #3498db;
}

/* Recipe grid (recipe cards) */
.recipe-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px;
  justify-items: center;
}

.recipe-card {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 300px;
  padding: 20px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.recipe-card img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  object-fit: cover;
  margin-bottom: 15px;
}

.recipe-card h3 {
  font-size: 1.2rem;
  color: #333;
  margin: 0;
  font-weight: 600;
}

.recipe-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

/* Dark Mode Styles */
body.dark-mode {
  background-color: #1e1e1e;
  color: #f5f5f5;
}

body.dark-mode .recipe-card {
  background-color: #34495e;
  color: #ecf0f1;
}

body.dark-mode .recipe-card h3 {
  color: #ecf0f1;
}

body.dark-mode .recipe-card img {
  border-radius: 12px;
  opacity: 0.9;
}

.noRecipesFound {
  text-align: center;
  color: #555;
}
