body {
  font-family: 'Inter', sans-serif;
  background-color: #fff;
  color: #333;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.recipe-details {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: left;
  font-family: 'Roboto', sans-serif;
}

.recipe-header {
  text-align: center;
  margin-bottom: 30px;
}

.recipe-image {
  width: 100%;
  max-height: 450px;
  object-fit: cover;
  border-radius: 10px;
}

.recipe-title {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-top: 20px;
  font-weight: bold;
}

.recipe-meta {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  font-size: 1.2rem;
  font-weight: 500;
  color: #7f8c8d;
}

.recipe-ingredients,
.recipe-instructions {
  margin: 20px 0;
}

.recipe-ingredients h2,
.recipe-instructions h2 {
  font-size: 1.8rem;
  color: #34495e;
  border-bottom: 2px solid #3498db;
  padding-bottom: 5px;
  margin-bottom: 15px;
}

.recipe-ingredients ul {
  list-style-type: square;
  padding-left: 20px;
  margin: 10px 0;
  line-height: 1.6;
  color: #2c3e50;
}

.recipe-instructions div {
  line-height: 1.8;
  color: #2c3e50;
  font-size: 1.1rem;
}

.back-button {
  display: inline-block;
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-bottom: 30px;
}

.back-button:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

body.dark-mode .recipe-details {
  background-color: #34495e;
  color: #ecf0f1;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

body.dark-mode .recipe-title {
  color: #ecf0f1;
}

body.dark-mode .recipe-meta {
  color: #bdc3c7;
}

body.dark-mode .recipe-ingredients h2,
body.dark-mode .recipe-instructions h2 {
  color: #ecf0f1;
}

body.dark-mode .recipe-ingredients ul {
  color: #ecf0f1;
}

body.dark-mode .recipe-instructions div {
  color: #ecf0f1;
}

body.dark-mode .back-button {
  background-color: #2980b9;
}

body.dark-mode .back-button:hover {
  background-color: #1f6b99;
}

body.light-mode .recipe-card {
  background-color: #ffffff;
  color: #333;
  border: 1px solid #ddd;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

body.light-mode .recipe-card h3 {
  color: #2c3e50;
}

body.light-mode .recipe-card img {
  border-radius: 8px;
}

body.light-mode .recipe-card:hover {
  background-color: #f9f9f9;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

body.light-mode .back-button {
  background-color: #3498db;
}

body.light-mode .back-button:hover {
  background-color: #2980b9;
}
