.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 50px;
    max-width: 600px;
    margin: 0 auto;
    margin-top: 80px;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    text-align: center;
  }
 
  .main-header{
    border-radius: 15px;
  }
  .profile-container h1 {
    font-size: 2.5rem;
    color: #fff;
    padding-top: 20px;
    margin-bottom: 30px;
  }
  
  .profile-info {
    width: 100%;
    max-width: 400px;
  }
  
  .profile-info p {
    margin-bottom: 20px;
    font-size: 1.2rem;
    color: #333;
  }
  
  .input-group {
    width: 100%;
    margin-bottom: 20px;
    position: relative;
  }
  
  .input-field {
    width: 100%;
    padding: 12px;
    border-radius: 25px;
    border: 1px solid #ddd;
    font-size: 1rem;
    outline: none;
    transition: border 0.3s ease;
  }
  
  .input-field:focus {
    border-color: #3498db;
  }
  
  .password-toggle {
    background: none;
    border: none;
    color: #3498db;
    cursor: pointer;
    font-size: 0.9rem;
    margin-top: 10px;
  }
  
  .password-toggle:hover {
    text-decoration: underline;
  }
  
  .submit-btn {
    width: 100%;
    padding: 12px;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  .submit-btn:hover {
    background-color: #2980b9;
  }
  
  .error-message {
    color: #e74c3c;
    font-size: 0.9rem;
    margin-top: 10px;
  }
  
  body.dark-mode .profile-container {
    background-color: #34495e;
    color: #ecf0f1;
  }
  
  body.dark-mode .input-field {
    background-color: #2c3e50;
    color: #ecf0f1;
    border: 1px solid #7f8c8d;
  }
  
  body.dark-mode .submit-btn {
    background-color: #2980b9;
  }
  
  body.dark-mode .submit-btn:hover {
    background-color: #1f6b99;
  }
  
  body.dark-mode .password-toggle {
    color: #ecf0f1;
  }
  